
.orderListWrapper {
    height: 600px;
    overflow-y: scroll;
    padding-right: 10px;
}

.orderListWrapper::-webkit-scrollbar-thumb {
    background-color: #8585AD;

}

.orderListWrapper::-webkit-scrollbar {
    width: 8px;
}

.orderListWrapper::-webkit-scrollbar-track {
    background: #2F2F37;
}